export const SYMBOL = '=';

export default function func(exp1, exp2) {
  if ((typeof exp1 === 'string') && (typeof exp2 === 'string')) {
    return exp1.toUpperCase() === exp2.toUpperCase();
  } else if ((exp1 == false || exp1 === undefined) && (exp2 == false || exp2 === undefined)) {
    return true;
  } else {
    return exp1 === exp2;
  }
};

func.SYMBOL = SYMBOL;



